import React, { Component } from 'react'
import "./PastBar.css"
class PastBar extends Component {
    render() {
        return (
            <div className="childbar-main">
                <div className="childbar-box">
                    <span></span>
                    <p>{this.props.data.title}</p>
                </div>
            </div>
        )
    }
}

export default PastBar
