import axios from 'axios'
import React, { Component } from 'react'
import { Link } from "react-router-dom"
import "./TrackBox.css"
export class TrackBox extends Component {
    constructor(props){
        super(props)
        this.state={
            isLiked: false,
            isClicked: false,
            likes: this.props.data.likes
        }
        this.handleLike = this.handleLike.bind(this)
        this.handleDislike = this.handleDislike.bind(this)
    }
    async handleLike(){
        this.setState({isLiked:true})
        try {
            await axios.get('/trackAction/',{params: {id: this.props.data.selfId, action: 'like'}})
            this.setState({likes: this.state.likes + 1})
        } catch (error) {
            this.setState({isLiked: false})
        }
    }
    async handleDislike(){
        this.setState({isLiked:false})
        try {
            await axios.get('/trackAction/',{params: {id: this.props.data.selfId, action: 'dislike'}})
            this.setState({likes: this.state.likes - 1})
        } catch (error) {
            this.setState({isLiked: true})
        }
    }
    render() {
        return (
            <div className="tbc-main">
                <div className="tbc-head">
                    <p><b>{this.props.data.title}</b></p>
                    {this.state.isLiked?
                        <img height="20px" width="20px" className="ccc-like-btn" onClick={this.handleDislike} src="/images/fill-heart.png" alt="fill-heart"/>
                    :
                        <img height="20px" width="20px" onClick={this.handleLike} src="/images/empty-heart.png" style={{opacity: '0.8'}} alt="empty-heart"/>
                    }
                </div>
                <div className="tbc-body">
                    <div className="tbc-detail">
                        <p>{this.props.data.desc}</p>
                    </div>
                    <div className="tbc-button-box">
                        <span>{this.state.likes} Likes</span>
                        <Link to={"/track/"+this.props.data.selfId}>
                            <button>Open Track</button>
                        </Link>
                        <Link to={"/course/"+this.props.data.selfId}>
                            <button>Resoures</button>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }
}

export default TrackBox
